import React, { Component } from 'react';
import './index.css';

export default class SocialHub extends Component {

    render() {

        const { lng = {} } = this.props;

        return(
            <div className="social-hub">
                <a href="https://octanium-os.blogspot.com" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToBlog}  className="fas fa-pen social-icon" target="_blank" title={lng.SocialHubBlogTitle} > </a>
                <a href="https://t.me/Octanium" rel="noopener noreferrer " aria-label={lng.SocialHubLinkToTelegram} className="fab fa-telegram social-icon" target="_blank" title={lng.SocialHubTelegramTitle} > </a>
                <a href="https://github.com/Octanium91" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToGithub} className="fab fa-github social-icon" target="_blank" title={lng.SocialHubGithubTitle} > </a>
                <a href="https://www.instagram.com/octanium91/" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToInstagram} className="fab fa-instagram social-icon" target="_blank" title={lng.SocialHubInstagramTitle} > </a>
                <a href="https://www.facebook.com/Octanium91" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToFacebook} className="fab fa-facebook social-icon" target="_blank" title={lng.SocialHubFacebookTitle} > </a>
                <a href="https://twitter.com/octanium91" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToTwitter} className="fab fa-twitter social-icon" target="_blank" title={lng.SocialHubTwitterTitle} > </a>
                <a href="https://www.youtube.com/octanium91" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToYoutube} className="fas fa-play social-icon" target="_blank" title={lng.SocialHubYoutubeTitle} > </a>
                <a href="https://www.linkedin.com/in/%D1%81%D0%B2%D1%8F%D1%82%D0%BE%D1%81%D0%BB%D0%B0%D0%B2-%D0%BF%D0%B0%D0%BB%D1%8F%D0%BD%D0%B8%D1%87%D0%BA%D0%B0-73ab7619b/" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToLinkedIn} className="fab fa-linkedin-in social-icon" target="_blank" title={lng.SocialHubLinkedInTitle} > </a>
            </div>
        );
    };
};