import React, { Component } from 'react';
import './index.css';

export default class Spec extends Component {

    render() {
        const { MainState } = this.props;

        return(
            <div className="spec">
                <p className="spec-text">{MainState.Lng.SpecText}</p>
                <div className="spec-info">
                    <span className="spec-info-item">{MainState.Lng.SpecCountry}</span>
                    <span className="spec-info-item">{MainState.Lng.SpecCity}</span>
                    <span className="spec-info-item">{MainState.Lng.SpecTel}<a className="spec-link" href="tel:+380953853631">+38-095-38-53-631</a></span>
                    <span className="spec-info-item">{MainState.Lng.SpecEmail}<a className="spec-link" href="mailto:octanum91@gmail.com">octanum91@gmail.com</a></span>
                    <span className="spec-info-item">{MainState.Lng.SpecAge+(new Date().getFullYear()-1992).toString()+MainState.Lng.SpecAgeYears}</span>
                    <span className="spec-info-item">{MainState.Lng.SpecIDE}</span>
                    <span className="spec-info-item">{MainState.Lng.SpecPL+"Java, Groovy, Spring, SQL, JS, HTML, CSS, ReactJS, VueJS"}</span>
                    <span className="spec-info-item">{MainState.Lng.SpecTech+"Docker, Bitbucket, GitHub, GitHub Action (CI), TeamCity (CI), Heroku, Microservices architecture, MongoDB, MySQL"}</span>
                </div>
            </div>
        );
    };
};